html,
body {
  height: 100%;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    Fira Sans,
    Droid Sans,
    Helvetica Neue,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea, #764ba2);
  min-height: 100vh;
}
#root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}
.app-container {
  width: 100%;
  max-width: 800px;
  min-height: 600px;
  background: #fff;
  border-radius: 20px;
  box-shadow:
    0 20px 40px #0000001a,
    0 8px 16px #00000014;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.header {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: #fff;
  padding: 40px 32px;
  text-align: center;
}
.header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 8px;
  letter-spacing: -0.5px;
}
.header p {
  font-size: 1.1rem;
  opacity: 0.9;
  font-weight: 400;
}
.progress-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 32px 40px 0;
  background: #fff;
  position: relative;
}
.progress-bar:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 80px;
  right: 80px;
  height: 2px;
  background: #e2e8f0;
  transform: translateY(-50%);
  z-index: 1;
}
.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;
  flex: 1;
  max-width: 140px;
}
.step-number {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #f8fafc;
  color: #94a3b8;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 12px;
  border: 3px solid white;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px #0000001a;
}
.step.active .step-number {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: #fff;
  transform: scale(1.05);
  box-shadow: 0 4px 12px #667eea4d;
}
.step span {
  font-size: 0.9rem;
  color: #64748b;
  font-weight: 500;
  text-align: center;
}
.step.active span {
  color: #667eea;
  font-weight: 600;
}
.content {
  flex: 1;
  padding: 0 40px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 400px;
}
.step-container {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}
.card {
  background: #fff;
  border-radius: 16px;
  padding: 40px;
  text-align: center;
  box-shadow: 0 4px 20px #00000014;
  border: 1px solid #f1f5f9;
}
.step-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
  gap: 16px;
}
.step-header h2 {
  color: #1e293b;
  font-size: 1.75rem;
  font-weight: 600;
  margin: 0;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
}
.form-group {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.form-label {
  font-weight: 500;
  color: #374151;
  margin-bottom: 8px;
  font-size: 0.95rem;
}
.form-select,
.field-select {
  width: 100%;
  padding: 14px 16px;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #fff;
  color: #374151;
}
.form-select:focus,
.field-select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px #667eea1a;
}
.btn-primary {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: #fff;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px #667eea4d;
}
.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #667eea66;
}
.btn-primary:active:not(:disabled) {
  transform: translateY(0);
}
.btn-primary:disabled {
  background: #9ca3af;
  transform: none;
  box-shadow: none;
  cursor: not-allowed;
}
.btn-primary.large {
  padding: 18px 40px;
  font-size: 1.1rem;
  border-radius: 14px;
}
.btn-text {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  font-size: 0.9rem;
  text-decoration: underline;
  padding: 8px 12px;
  border-radius: 6px;
  transition: color 0.3s ease;
}
.btn-text:hover {
  color: #667eea;
  background: #f3f4f6;
}
.file-upload-area {
  border: 2px dashed #d1d5db;
  border-radius: 16px;
  padding: 48px 32px;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  background: #fafafa;
}
.file-upload-area:hover {
  border-color: #667eea;
  background: #f8faff;
}
.upload-icon {
  font-size: 3rem;
  margin-bottom: 16px;
  opacity: 0.7;
}
.upload-text strong {
  display: block;
  color: #1f2937;
  margin-bottom: 8px;
  font-size: 1.2rem;
}
.upload-text span {
  color: #6b7280;
  font-size: 0.95rem;
}
.file-info {
  margin-top: 20px;
  padding: 16px;
  background: #f0f9ff;
  border: 1px solid #bae6fd;
  border-radius: 12px;
  color: #0369a1;
  font-weight: 500;
}
.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
  padding: 24px;
  background: #f8fafc;
  border-radius: 12px;
  border: 1px solid #e2e8f0;
}
.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.info-item label {
  font-size: 0.85rem;
  color: #6b7280;
  margin-bottom: 6px;
  font-weight: 500;
}
.info-item span {
  font-weight: 600;
  color: #1f2937;
  font-size: 1rem;
}
.mapping-container {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 32px;
  box-shadow: 0 2px 8px #0000000d;
}
.mapping-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #f9fafb;
  padding: 20px 24px;
  font-weight: 600;
  color: #374151;
  border-bottom: 1px solid #e5e7eb;
}
.mapping-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px 24px;
  border-bottom: 1px solid #f3f4f6;
  align-items: center;
  transition: background 0.2s ease;
}
.mapping-row:hover {
  background: #f9fafb;
}
.mapping-col {
  padding: 0 12px;
}
.excel-field {
  background: #f3f4f6;
  padding: 12px 16px;
  border-radius: 8px;
  font-family:
    Courier New,
    monospace;
  font-size: 0.95rem;
  color: #1f2937;
  border: 1px solid #e5e7eb;
}
.loading-overlay {
  position: fixed;
  inset: 0;
  background: #fffffff2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #f3f4f6;
  border-top: 4px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}
.loading-overlay p {
  color: #6b7280;
  font-size: 1.1rem;
  font-weight: 500;
}
.process-info {
  text-align: center;
  color: #6b7280;
  margin-bottom: 24px;
  font-size: 1rem;
}
.process-info strong {
  color: #667eea;
}
@media (max-width: 768px) {
  #root {
    padding: 16px;
    align-items: flex-start;
  }
  .app-container {
    min-height: auto;
    border-radius: 16px;
  }
  .header {
    padding: 32px 24px;
  }
  .header h1 {
    font-size: 2rem;
  }
  .progress-bar {
    padding: 24px 20px 0;
    flex-direction: column;
    gap: 20px;
  }
  .progress-bar:before {
    display: none;
  }
  .step {
    flex-direction: row;
    gap: 16px;
    max-width: none;
    width: 100%;
    justify-content: flex-start;
  }
  .step-number {
    margin-bottom: 0;
    flex-shrink: 0;
  }
  .content {
    padding: 0 24px 32px;
    min-height: 300px;
  }
  .card {
    padding: 32px 24px;
  }
  .step-header {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }
  .step-header h2 {
    font-size: 1.5rem;
  }
  .mapping-header,
  .mapping-row {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  .mapping-header {
    display: none;
  }
  .mapping-row {
    padding: 16px;
    background: #f9fafb;
    border-radius: 8px;
    margin-bottom: 12px;
    border: 1px solid #e5e7eb;
  }
  .info-grid {
    grid-template-columns: 1fr;
    gap: 16px;
    padding: 20px;
  }
  .file-upload-area {
    padding: 32px 20px;
  }
}
@media (max-width: 480px) {
  .header {
    padding: 24px 20px;
  }
  .header h1 {
    font-size: 1.75rem;
  }
  .content {
    padding: 0 16px 24px;
  }
  .card {
    padding: 24px 20px;
  }
  .btn-primary {
    padding: 14px 24px;
  }
}
