@media (max-width: 768px) {
  .app-container {
    padding: 16px;
  }
  .header h1 {
    font-size: 2rem;
  }
  .card {
    padding: 24px;
  }
  .mapping-header,
  .mapping-row {
    grid-template-columns: 1fr;
    gap: 12px;
  }
  .progress-bar {
    flex-direction: column;
    gap: 16px;
    align-items: flex-start;
  }
  .progress-bar:before {
    display: none;
  }
  .step {
    flex-direction: row;
    gap: 12px;
    max-width: none;
  }
}
